@media only screen and (max-width: 700px) {
  /* For mobile phones: */
  .Button-Box {
    margin-left: 65px !important;
  }
}
@media only screen and (max-width: 411px) {
  /* For mobile phones: */
  .Button-Box {
    margin-left: 100px !important;
  }
}

.Button-Box {
  background: rgb(252, 250, 250);
  border: 1px solid #e9e7e7;
  border-radius: 25px;
  width: 202px;
  height: 320px;
  margin-left: 500px;
  margin-top: 100px;
}

.button {
  outline: 0 !important;
  border-radius: 50%;
  border: 1px solid #ffffff;
  background-color: rgb(255, 255, 255);
  color: #07d4de;
  margin-top: 5px;
  margin-left: 10px;
  margin-right: 10px;
}

.tbox {
  width: 160px;
  font-size: 20px;
  background: none;
  color: #07d4de;
  text-align: right;
  border-radius: 20%;
  border: none;
  outline: none;
  margin-top: 10px;
  margin-left: 10px;
  margin-bottom: 10px;
}

.rbox {
  font-size: 40px;
}

.but {
  outline: 0 !important;
  border-radius: 50%;
  border: 1px solid #ffffff;
  background-color: #3db0f3fd;
  color: white;
  margin-top: 5px;
  margin-left: 12px;
  margin-right: 10px;
}
